import React, { useState, useEffect, useRef } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {PanelHeader, Panel, CardGrid, Card, Group, Button, Banner, Avatar, Header, TabsItem, Tabs, Cell} from "@vkontakte/vkui";
import Icon56DownloadSquareOutline from '@vkontakte/icons/dist/56/download_square_outline';

const Home = ({id, goTo, goBack, fetchedUser, setImg, images }) => {
    const [activeTab, setActiveTab] = useState('memes');
    return (
        <Panel id={id}>
            <PanelHeader separator={false}>
                Memes
            </PanelHeader>
            <Tabs>
                <TabsItem
                    onClick={() => setActiveTab('memes')}
                    selected={activeTab === 'memes'}
                >
                    Мемасики
                </TabsItem>
                <TabsItem
                    onClick={() => setActiveTab('about')}
                    selected={activeTab === 'about'}
                >
                    О пользователе
                </TabsItem>
            </Tabs>
            {
                activeTab === 'memes' &&
                <React.Fragment>
                    <Banner
                        before={<Avatar size={96}><Icon56DownloadSquareOutline /></Avatar>}
                        header="Индивидуальный"
                        subheader="Создать мем на основе Вашего фона."
                        actions={<Button onClick={()=>goTo('library')}>Выбрать</Button>}
                    />
                    <Banner
                        before={<Avatar size={96} mode="image" src="https://sun9-2.userapi.com/9zeJTHhDUrMoa68qJSXbQ9kvxllYaR-hznQKSA/YW69HXGyYKA.jpg" />}
                        header="Выбрать фон"
                        subheader="Олдовые фоны для мемов из нашей коллекции."
                        actions={<Button onClick={()=>goTo('library')}>Выбрать</Button>}
                    />
                    {/*<Banner*/}
                    {/*    before={<Avatar size={96} mode="image" src="https://sun9-32.userapi.com/ParIQVaDXNxPzFTeh4EfL7Okamle-0ZvmvRRcg/N2NXEDHfEbs.jpg" />}*/}
                    {/*    header="Квадраты"*/}
                    {/*    subheader="Два квадрата справа для текста"*/}
                    {/*    //actions={<Button onClick={()=>goTo('library')}>Выбрать</Button>}*/}
                    {/*/>*/}
                    {/*<Banner*/}
                    {/*    before={<Avatar size={96} mode="image" src="https://sun9-14.userapi.com/CNBgsxFXMRFS7UPJNyUfxKb_krOhqLrDc3sZtQ/-h_uYiLGLcA.jpg" />}*/}
                    {/*    header="Двустрочный"*/}
                    {/*    subheader="Мем с двумя строками"*/}
                    {/*    actions={<Button onClick={()=>goTo('library')}>Выбрать</Button>}*/}
                    {/*/>*/}
                    <Group separator="hide" header={<Header mode="secondary">Новые фоны</Header>}>
                        <CardGrid>
                            {
                                images.map((image, index)=>{
                                    return (
                                        <Card
                                            key={index}
                                            style={{padding: 2, borderRadius: 13, margin: 0}}
                                            size="s"
                                            onClick={()=>{
                                                setImg(image);
                                                goTo('constructor')
                                            }}
                                        >
                                            <div style={{
                                                height: 96,
                                                backgroundImage: 'url(' + image + ')',
                                                backgroundSize: 'cover',
                                                backgroundPosition: 'center 35%',
                                                backgroundRepeat: 'no-repeat',
                                                borderRadius: 13
                                            }} />
                                        </Card>
                                    )
                                })
                            }
                        </CardGrid>
                    </Group>
                </React.Fragment>
            }
            {
                activeTab === 'about' &&
                    <Cell>djn</Cell>
            }
        </Panel>
    );
}

export default Home;