import React, { useState, useEffect, useRef } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import {
    PanelHeader, Panel, PanelHeaderBack, Cell
    , Div, Button, Input, FormLayout, Slider, Tabs, TabsItem, CardGrid, Card, Group, Avatar, Banner
} from "@vkontakte/vkui";
import Icon24LogoInstagram from '@vkontakte/icons/dist/24/logo_instagram';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24Attachments from '@vkontakte/icons/dist/24/attachments';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';
import Icon56DownloadSquareOutline from "@vkontakte/icons/dist/56/download_square_outline";

const Library = ({id, goTo, goBack, fetchedUser, setImg, images}) => {

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goBack} />}>
                Выберите фон
            </PanelHeader>
            <Group separator="hide">
                <Banner
                    before={<Avatar size={96}><Icon56DownloadSquareOutline /></Avatar>}
                    header="Предложи свой фон"
                    subheader="Загрузи фон для мемасов, чтобы разместить его в библиотеке. На картинке должны отсутствовать надписи и водные знаки."
                    actions={<Button onClick={()=>console.log('load')}>Загрузить</Button>}
                />
                <CardGrid>
                    {
                        images.map((image, index)=>{
                            return (
                                <Card
                                    key={index}
                                    style={{padding: 2, borderRadius: 13, margin: 0}}
                                    size="s"
                                    onClick={()=>{
                                        setImg(image);
                                        goTo('constructor')
                                    }}
                                >
                                    <div style={{
                                        height: 96,
                                        backgroundImage: 'url(' + image + ')',
                                        backgroundSize: 'cover',
                                        backgroundPosition: 'center 35%',
                                        backgroundRepeat: 'no-repeat',
                                        borderRadius: 13
                                    }} />
                                </Card>
                            )
                        })
                    }
                </CardGrid>
            </Group>
        </Panel>
    );
}

export default Library;