import React, { useState, useEffect, useRef } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import {
    PanelHeader, Panel, PanelHeaderBack, Cell
    , Div, Button, Input, FormLayout, Slider, Tabs, TabsItem
} from "@vkontakte/vkui";
import Icon24LogoInstagram from '@vkontakte/icons/dist/24/logo_instagram';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24Attachments from '@vkontakte/icons/dist/24/attachments';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';

const Constructor = ({id, image, goTo, goBack}) => {
    let schema = new Image()
    schema.src = image
    const [img, setImg] = useState(schema);
    const canvasRef = React.useRef(null)
    const [top, setTop] = useState('')
    const [bottom, setBottom] = useState('')
    const [genStatus, setGenStatus] = useState(false)
    const [memes, setMemes] = useState(image)
    const [fontH, setFontH] = useState(30)
    const [fontС, setFontС] = useState('white')
    const [heightCanvas, setHeightCanvas] = useState(0)

    useEffect(()=>{
        generate()
    })

    function writeTop(text) {
        const canvas = canvasRef.current
        const ctx = canvas.getContext('2d')
        ctx.fillStyle = fontС;
        ctx.font = 'bold '+fontH+'px impact';
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 6;
        let lineHeight = getFontHeight(ctx.font);
        let maxWidth = canvas.width;
        let marginTop = getFontHeight(ctx.font);
        let words = text.split(" ");
        let countWords = words.length;
        let line = "";
        for (let n = 0; n < countWords; n++) {
            let testLine = line + words[n] + " ";
            let testWidth = ctx.measureText(testLine).width;
            if (testWidth > maxWidth) {
                let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
                ctx.fillText(line, marginLeft, marginTop);
                line = words[n] + " ";
                marginTop += lineHeight;
            }
            else {
                line = testLine;
            }
        }
        let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
        ctx.strokeText(line, marginLeft, marginTop);
        ctx.fillText(line, marginLeft, marginTop);
    }
    function writeBottom(text) {
        const canvas = canvasRef.current
        const ctx = canvas.getContext('2d')
        let maxWidth = canvas.width;
        let marginTop;
        ctx.font = 'bold '+fontH+'px impact';
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 6;
        ctx.measureText(text).width > maxWidth ?
            marginTop = canvas.height - getFontHeight(ctx.font)-10 :
            marginTop = canvas.height - 10
        ctx.fillStyle = fontС;
        let lineHeight = getFontHeight(ctx.font);
        let words = text.split(" ");
        let countWords = words.length;
        let line = "";
        for (let n = 0; n < countWords; n++) {
            let testLine = line + words[n] + " ";
            let testWidth = ctx.measureText(testLine).width;
            if (testWidth > maxWidth) {
                let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
                ctx.fillText(line, marginLeft, marginTop);
                line = words[n] + " ";
                marginTop += lineHeight;
            }
            else {
                line = testLine;
            }
        }
        let marginLeft = ((canvas.width - ctx.measureText(line).width)/2)
        ctx.strokeText(line, marginLeft, marginTop);
        ctx.fillText(line, marginLeft, marginTop);
    }
    function writeGroup() {
        const canvas = canvasRef.current
        const ctx = canvas.getContext('2d')
        ctx.font = '10px Arial';
        ctx.fillStyle = '#FFF';
        let marginTop = canvas.height - 2
        let marginLeft = 0
        ctx.strokeStyle = 'black';
        ctx.lineWidth = 4;
        ctx.strokeText('https://vk.com/gen_memes', marginLeft, marginTop);
        ctx.fillText('https://vk.com/gen_memes', marginLeft, marginTop);
    }
    function getFontHeight(font) {
        let parent = document.createElement("span");
        parent.appendChild(document.createTextNode("height"));
        document.body.appendChild(parent);
        parent.style.cssText = "font: " + font + "; white-space: nowrap; display: inline;";
        let height = parent.offsetHeight;
        document.body.removeChild(parent);
        return height;
    }
    function generate(){
        let imm = document.getElementById('kart');
        let rotate = imm.height / imm.width
        setHeightCanvas(window.innerWidth*rotate)

        setGenStatus(true)
        const canvas = canvasRef.current
        const ctx = canvas.getContext('2d')
        ctx.clearRect(0,0,canvas.width,canvas.height)
        imm.style.width = window.innerWidth
        imm.style.height = window.innerWidth * rotate
        ctx.drawImage(imm, 0, 0, window.innerWidth, window.innerWidth * rotate)
        writeTop(top)
        writeBottom(bottom)
        writeGroup()
        setMemes(canvas.toDataURL("image/png"))
    }

    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goBack} />}>
                Создание мема
            </PanelHeader>
            <img style={{display: 'none'}} crossOrigin={'anonymous'} id={'kart'} src={image}/>
            {/*<img width={window.innerWidth} height={image.height} src={memes} />*/}
            <canvas
                //style={{display: 'none'}}
                ref={canvasRef}
                width={window.innerWidth}
                height={heightCanvas}
            />
            <FormLayout>
                <Slider
                    min={10}
                    max={100}
                    value={Number(fontH)}
                    onChange={fontH=>setFontH(fontH)}
                    top="Размер шрифта"
                />
                <Tabs>
                    <TabsItem
                        onClick={()=>setFontС('white')}
                        selected={fontС === 'white'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'white', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                    <TabsItem
                        onClick={()=>setFontС('black')}
                        selected={fontС === 'black'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'black', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                    <TabsItem
                        onClick={()=>setFontС('red')}
                        selected={fontС === 'red'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'red', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                    <TabsItem
                        onClick={()=>setFontС('blue')}
                        selected={fontС === 'blue'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'blue', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                    <TabsItem
                        onClick={()=>setFontС('chocolate')}
                        selected={fontС === 'chocolate'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'chocolate', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                    <TabsItem
                        onClick={()=>setFontС('lightseagreen')}
                        selected={fontС === 'lightseagreen'}
                    >
                        <Div style={{height: 5, width: 5, borderRadius: 50, backgroundColor: 'lightseagreen', border: '2px solid darkslateblue', margin: 'auto'}}/>
                    </TabsItem>
                </Tabs>
                    <Input
                        status={
                            top.length>0 ?
                                top.length < 50 ? 'valid' : 'error' :
                                null
                        }
                        bottom={top.length+'/50'}
                        top="Текст верхней строки"
                        placeholder={'Напишите, или оставьте поле пустым'}
                        value={top}
                        onChange={e=>setTop(e.target.value)}/>
                    <Input
                        status={
                            bottom.length>0 ?
                                bottom.length < 50 ? 'valid' : 'error' :
                                null
                        }
                        bottom={bottom.length+'/50'}
                        top="Текст нижней строки"
                        placeholder={'Напишите, или оставьте поле пустым'}
                        value={bottom}
                        onChange={e=>setBottom(e.target.value)}/>
                        <Cell multiline>Обратите внимание: для корректного переноса строк между словами обязательно должны быть пробелы.</Cell>
            </FormLayout>
            {
                top !== '' || bottom !== '' ?
                    <React.Fragment>
                        <Div style={{display: 'flex'}}>
                            <Button before={<Icon24ShareOutline/>} size="l" stretched style={{ marginRight: 8 }}>На стену</Button>
                            <Button before={<Icon24LogoInstagram/>} size="l" stretched>В историю</Button>
                        </Div>
                        <Div>
                            <Button before={<Icon24Attachments/>} size="xl">Сохранить в библиотеке</Button>
                        </Div>
                    </React.Fragment> :
                    null
            }
        </Panel>
    );
}

export default Constructor;